#cards {
  box-sizing: border-box;
  padding: 5px;
  margin: 20px 50px 20px 50px;
  border: 2px solid black;
  flex-basis: 40%;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.head,
.para {
  display: flex;
  /* flex: 1/-1; */
}

.btn {
  border: 1px solid blue;
  margin: 2px;
  background-color: rgb(167, 167, 255);
}
